import React from 'react';
import { colors, fonts } from '../constants';
import RightArrowIcon from './icons/RightArrow';
import Loading from './Loading';

export default ({
  className = '',
  children,
  large,
  hasArrow,
  primary,
  isFullWidth,
  loading,
  ...props
}) => (
  <button
    className={`button ${
      hasArrow && !isFullWidth ? 'button__with-arrow' : ''
    } ${isFullWidth ? 'button__full' : ''} ${className}`}
    {...props}
  >
    {children}
    {loading && <Loading when={loading} />}
    {hasArrow && (
      <span className="button__arrow-icon">
        <RightArrowIcon
          color={primary ? colors.secondary : colors.primary}
          backgroundColor={primary ? colors.primary : colors.secondary}
        />
      </span>
    )}
    <style jsx>{`
      .button {
        font-size: ${large ? '20' : '17'}px;
        font-weight: normal;
        font-family: ${fonts.halyardDisplay};
        color: ${primary ? colors.secondary : colors.primary};
        background-color: ${primary ? colors.primary : colors.secondary};
        position: relative;

        height: ${large ? '56' : '48'}px;
        line-height: ${large ? '56' : '48'}px;
        padding: 0 24px;
        border: 0;
        box-shadow: 0px 2px 5px rgba(60, 77, 87, 0.1),
          0px 1px 1px rgba(60, 66, 87, 0.201459);
        align-items: center;
        text-align: center;
        letter-spacing: 0.5px;
        border-radius: 4px;
      }

      .button:hover:enabled {
        cursor: pointer;
        background-color: ${primary ? 'rgba(31, 50, 93, 0.95)' : '#fafafa;'};
      }

      .button:disabled {
        background-color: ${primary ? 'rgba(31, 50, 93, 0.5)' : '#ccc'};
      }

      .button__with-arrow {
        padding-right: 52px;
      }

      .button__full {
        width: 100%;
      }

      .button__arrow-icon {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 16px;
        line-height: 0;
      }
    `}</style>
  </button>
);
